.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.toast-container {
    position: 'absolute';
    min-height: '200px';
    top: 0;
    right: 0;
}

.form-container {
  padding-top: 50px;
}
.form-input-title {
  color: black;
  font-weight: bold;
  font-size: medium;
  text-align: left;
  display: block;
}

.form-input-container {
  border: solid #f7f7f9;
  margin-bottom: 50px;
  padding: 25px;
}

.form-group {
  border: none;
  box-shadow: none;
  background-color: #f0f0f0;
  padding: 20px;
}

.result-container {
  border: solid #f7f7f9;
  margin-bottom: 50px;
  padding: 25px;
  min-height: 500px;
}

.loading-container {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.1);
  z-index: 2;
  cursor: pointer;
}

.loading-content {
  position: absolute;
  top: 50%;
  left: 50%;
  /* transform: translate(-50%, -50%); */
}

.form-file-label {
  text-align: start;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
